@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR&display=swap');

.main-page-container {
    font-family: "IBM Plex Sans KR", serif;
    font-weight: 400;
    font-style: normal;
    position: relative;
    width:70%;
    height:0;
    padding-bottom:45%;
    margin: 0 auto;
    margin-top:80px;
    margin-left:25%;
}

.main-page-container > h3 {
    margin:0;
    padding:0;
    color: #f6f6f6;
    font-size:40px;
    transition: all 0.5s;
}

.main-page-container > h5 {
    font-weight:300;
    color: #f6f6f6;
    font-size:18px;
    margin:0;
    padding:0;
    margin-top:20px;
    margin-left:20px;
    transition: all 0.5s;
}

#main-page-box{
    position: relative;
    width:100%;
    height:0;
    padding-bottom:45%;
    margin-top:50px;
}

#main-page-left-box{
    position:absolute;
    width:40%;
    height:90%;
    /*
    background:url(../../img/Page/me.png) no-repeat;*/
    background:url(../../img/Page/info/me.png) no-repeat;
    background-size: cover;
    border-radius: 15px;
}

#main-page-right-box{
    position: absolute;
    left:40%;
    width:60%;
    height: 90%;
    color:white;
}

#main-page-right-box>ul {
    margin:0;
    margin-left:20px;
}

#main-page-right-box>ul>li>ul{
    margin-bottom:5%;
}

.xx, .yy {
    padding:0;
    margin:0;
}

.yy {
    margin-top:10px;
    transition: all 0.5s;
}

.xx{
    margin-top:5%;
    transition: all 0.5s;
}

.lists {
    display:flex;
    margin-left:15px;
}

.lists > div{
    float:left;
}

.left-msg {
    width:20%;
    margin-top:1%;
    margin-right:20px;
    padding-bottom:1%;
    border-bottom: 2px solid white;
    border-right: 2px solid white;
    font-size:18px;
    line-height: 14px;
    transition: all 0.5s;
}
.right-msg {
    width:80%;
    font-size:15px;
    transition: all 0.5s;
}

#sites {
    margin-top:10px;
}

#sites > li {
    float:left;
}

#git-img {
    transition: all 0.6s;
    width:50px;
    height:50px;
    background:url(../../img/Page/info/github.png) no-repeat;
    /*
    background: url(../../img/Page/github.png) no-repeat;*/
}

#gm-img {
    margin-left:10px;
    border-radius: 50px;
    transition: all 0.6s;
    width:50px;
    height:50px;
    background:url(../../img/Page/info/logo_small.PNG) no-repeat;
    /*
    background: url(../../img/Page/logo.png) no-repeat;*/
}

#acm-img {
    margin-left:10px;
    border-radius: 50px;
    transition: all 0.6s;
    width:50px;
    height:50px;
    background:url(../../img/Page/info/acmicpc_small.png) no-repeat;
    /*
    background: url(../../img/page/mainPage/acmicpc.png) no-repeat;*/
}

#git-img:hover {
    /*
    background: url(../../img/page/mainPage/github_hover.png) no-repeat;*/
    background:url(../../img/Page/info/github_hover.png) no-repeat;
}

#gm-img:hover {
    /*
    background: url(../../img/page/mainPage/logo_hover.png) no-repeat;*/
    background:url(../../img/Page/info/logo_small_hover.png) no-repeat;
}

@media (max-width:1324px){
    .main-page-container > h3 {
        font-size:30px;
    }
    .main-page-container > h5 {
        font-size:16px;
    }
    .left-msg {
        font-size:14px;
        margin-top:0.5%;
    }
    .right-msg{
        font-size:12px;
    }
    .xx{
        margin-top:0%;
    }
    .yy{
        margin-top:0px;
    }
    #main-page-right-box>ul>li>ul{
        margin-bottom:1%;
    }
}
@media (min-width:2000px) {
    .left-msg {
        font-size:25px;
        margin-top:0.5%;
        line-height:20px;
    }
    .right-msg{
        font-size:20px;
    }
    .xx{
        margin-top:10%;
        font-size:28px;
    }
    .yy{
        margin-top:10px;
        font-size:28px;
    }
    .main-page-container > h3 {
        font-size:60px;
    }
    
    .main-page-container > h5 {
        font-size:30px;
    }
}

#plus-button {
    width: 60px;
    height: 60px;
    border-radius: 100%;
    margin-top: 15%;
    margin-left:30%;
    font-size:60px;
    line-height: 60px;
    color:#d3d3d3;
    border:0px;
    transition: all 0.6s;
    background:transparent;
    box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.8),
    7px 7px 20px 0px rgba(0,0,0,.3),
    4px 4px 5px 0px rgba(0,0,0,.3);
}

#plus-button:hover {
    cursor: pointer;
    color:rgb(189, 189, 189);
    background-color: #525252;
}